@import "components/variables";
@import "components/mixins";
@import "components/header";
@import "components/tile";
@import "components/fonts";

.header{
    display: flex;
    align-items: center;
    padding: 0px 60px;
    background-color: #242424;
    a{
        color: #ffffff;
font-family: Righteous;
font-size: 22px;
font-weight: 400;
text-transform: uppercase;
        img{
            padding-right: 20px;

        }

    }
    .navbar-brand{
        display: flex;
    align-items: center;
        padding: 0;
    }
 .sub_share{
     display: none;
     position: absolute;
     box-shadow: 0 0 7px rgba(36, 36, 36, 0.29);
background-color: #ffffff;
z-index: 20;
     a{
         display: inline-flex;
        color: #242424;
        font-family: Righteous;
        font-size: 10px;
        font-weight: 400;
        text-transform: uppercase;
        position: relative;
        &::after{
            content: '';
            position: absolute;
            width: 100px;
            height: 1px;
            background: #000;
            background-color: #242424;
opacity: 0.1;
bottom: 0px;
        }
      
     }
 }   
 .right_side_menu{
    
    display: flex;
    justify-content: center;
    align-items: center;
     >li{
         position: relative;
         >a{
             padding: 15px 10px;
             display: block;
             height: 100%;
            font-size: 12px;
            font-weight: 400;
            text-transform: uppercase;
            opacity: 0.5;
            color: #9d9d9d;
            &:hover{
                        box-shadow: 0 0 7px rgba(36, 36, 36, 0.29);
        background-color: #1b1b1b;
        color: #fff;
        opacity: 1;
            }
        
         }
         &:hover ul.sub_share{
            display: block;
        }
     }
 }
}
.card{
    border:none;
    position: relative;
    border-radius: 0;
    padding-bottom: 0px;
    margin-bottom: 20px;
  .card_img{
      cursor: pointer;
      width: 100%;
      height: 15em;
      overflow: hidden;
      position: relative;
      &:hover .img_hover{
          display: flex;

      }
    img{
        height: 100%;

      
    }
    .img_hover{
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(36,36,36,0.5);
        z-index: 1000;
        display: none;
        justify-content: center;
        align-items: center;
        text-align: center;
        color: #41e9ff;
font-family: Righteous;
font-weight: 400;
text-transform: uppercase;
h1{
    font-size: 18px;
    border: 3px solid #41e9ff;
    padding: 5px;
}
        &::before,
        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            display: block;
            width: 200%;
            left:-190px;
            height: 10px;
            
        }
        &::before{
        
            background: linear-gradient(50deg, #fff 0%,#fff 50%,rgba(0,0,0,0) 50%,rgba(0,0,0,0) 100%);
        }
        &::after{
          
            background: linear-gradient(-50deg, #fff 0%,#fff 50%,rgba(0,0,0,0) 50%,rgba(0,0,0,0) 100%);
        }
    }
 
      &::before,
      &::after{
          content: '';
          position: absolute;
          bottom: 0;
          display: block;
          width: 200%;
          left:-190px;
          height: 10px;

      }
      &::before{
      
          background: linear-gradient(50deg, #fff 0%,#fff 50%,rgba(0,0,0,0) 50%,rgba(0,0,0,0) 100%);
      }
      &::after{
        
          background: linear-gradient(-50deg, #fff 0%,#fff 50%,rgba(0,0,0,0) 50%,rgba(0,0,0,0) 100%);
      }  }
      .card-info{
          display: flex;
          align-items: center;
          span{
            display: flex;
            align-items: center;        
            color: #9d9d9d;
            font-family: Philosopher;
            font-size: 11px;
            font-weight: 400;
            padding-bottom: 10px;
            img{
                margin-right: 10px;
                margin-left: 5px;
            }
          }
      }
      .card-body{
         padding-top: 0px; 
         padding-left: 5px;
         border-bottom: 6px solid #41e9ff;
         h5.card-title{
            color: #242424;
            font-family: Philosopher;
            font-size: 22px;
            font-weight: bold;
         }
         .card-text{
            font-size: 14px;
            font-weight: 400;
         }
      }
}
.more_news{
    text-align: center;
}
.load_more{
    display: inline-block;
    text-align: center;
    color: #41e9ff;
font-family: Righteous;
font-size: 18px;
font-weight: 400;
text-transform: uppercase;
border: 3px solid #41e9ff;
padding: 5px; 
    &:hover{
        text-decoration: none;
        color: #41e9ff;

    }
}
@media screen and (max-width: 780px){
    .card .card_img{
        height: 100vh;
    }
}